ass=\"adv-title\">\n              <text class=\"adv-title-text\">达人店最新推广素材</text>\n            </div>\n            <div class=\"adv-desc\">\n              <text class=\"adv-desc-text\">快来围观吧>></text>\n            </div>\n            <image class=\"adv-arr\" resize=\"cover\" :src=\"arrImg\"></image>\n            <image class=\"adv-img\" resize=\"cover\" :src=\"advImg\"></image>\n          </div>\n        </a>\n        <div v-else class=\"adv-main\">\n          <div class=\"adv-title\">\n            <text class=\"adv-title-text\">达人店最新推广素材</text>\n          </div>\n          <div class=\"adv-desc\">\n            <text class=\"adv-desc-text\">仅在app上可以使用哦</text>\n          </div>\n          <image class=\"adv-img\" resize=\"cover\" :src=\"advImg\"></image>\n        </div>\n      </div>\n  </div>\n  <div v-for=\"module in moduleList\">\n    <div class=\"goods-area\">\n      <div class=\"goods-title\">\n        <text class=\"goods-title-line\">—</text>\n        <text class=\"goods-title-text\">{{module.mainTitle}}</text>\n        <text class=\"goods-title-line\">—</text>\n      </div>\n      <div class=\"goods-desc\">\n        <text class=\"goods-desc-text\">{{module.subHead}}</text>\n      </div>\n        <scroller class=\"goods-scroller\" scrollDirection=\"horizontal\">\n          <div class=\"goods-container\" v-for=\"sku in module.skuInfos\">\n          <!-- <a :href=\"'/shop/sku/'+(sku.skuId)+'.html?shopId='+module.shopId\"> -->\n          <div @click=\"goodsDetailAction(sku.skuId, module.shopId)\">\n            <image class=\"goods-img\" resize=\"cover\" :src=\"sku.imgUrl+'.300x300.png'\"></image>\n            <div class=\"goods-name\">\n              <text class=\"goods-name-text\" lines=\"1\">[{{sku.brand}}]{{sku.skuName}}</text>\n            </div>\n            <div class=\"goods-price\">\n              <text class=\"goods-price-unit\">￥</text>\n              <text class=\"goods-price-text\">{{sku.shopPrice}}</text>\n            </div>\n            <div class=\"goods-comm\">\n              <text class=\"goods-comm-text\">赚￥{{sku.commission}}</text>\n            </div>\n          </div>\n          <!-- </a> -->\n          </div>\n          <a :href=\"'/shop/activityExposure/hotRecommendWeex.html?type='+module.type\">\n            <div class=\"all-container\">\n              <div class=\"all-area\">\n                <text class=\"all-title-text\">查看全部</text>\n                <text class=\"all-desc-text\">SEE ALL</text>\n              </div>\n            </div>\n          </a>\n        </scroller>\n    </div>\n  </div>\n</scroller>\n</template>\n\n<style scoped>\n  .page {\n    background-color: rgb(243, 243, 243);\n  }\n  .slider {\n    position: relative;\n    flex-direction: row;\n    width: 750px;\n    height: 375px;\n  }\n  .slider-wrap {\n    position: relative;\n    width: 750px;\n    height: 375px;\n  }\n  .slider-img {\n    width: 750px;\n    height: 375px;\n  }\n  .indicator {\n    display: -webkit-flex;\n    display: flex;\n    position: absolute;\n    width: 750px;\n    height: 375px;\n    top: 173px;\n    item-size: 10px;\n    item-color: rgba(255, 255, 255, 0.50);\n    item-selectedColor: #fff;\n  }\n  .banner {\n    position: relative;\n    flex-direction: row;\n    width: 750px;\n    height: 375px;\n  }\n  .banner-img {\n    width: 750px;\n    height: 375px;\n  }\n  .adv-area {\n    width: 750px;\n    height: 200px;\n    margin-bottom: 10px;\n    flex-direction: row;\n    justify-content: center;\n    align-items: center;\n    background-color: #fff;\n  }\n  .adv-img {\n    position: absolute;\n    top: -20px;\n    left: 10px;\n    width: 150px;\n    height: 140px;\n  }\n  .adv-main {\n    width: 540px;\n    height: 120px;\n    padding-left: 58px;\n    background-color: rgb(243, 243, 243);\n    border-radius: 60px;\n  }\n  .adv-title {\n    flex-direction: row;\n    align-content: center;\n    justify-content: center;\n    margin-top: 26px;\n    width: 540px;\n    height: 36px;\n  }\n  .adv-title-text {\n    text-align: center;\n    color: rgb(77, 77, 77);\n    font-size: 28px;\n  }\n  .adv-desc {\n    flex-direction: row;\n    align-content: center;\n    justify-content: center;\n    margin-top: 10px;\n    height: 30px;\n    width: 540px;\n  }\n  .adv-desc-text {\n    text-align: center;\n    font-size: 24px;\n    color: rgb(249, 52, 80);\n  }\n  .adv-arr {\n    position: absolute;\n    width: 15px;\n    height: 30px;\n    right: 40px;\n    top: 45px;\n  }\n  .goods-area {\n    margin-top: 10px;\n    width: 750px;\n    height: 552px;\n    background-color: #fff;\n  }\n  .goods-title {\n    flex-direction: row;\n    justify-content: center;\n    align-items: center;\n    margin-top: 32px;\n  }\n  .goods-title-text {\n    margin-left: 10px;\n    margin-right: 10px;\n    text-align: center;\n    font-size: 36px;\n    color: rgb(51, 51, 51);\n  }\n  .goods-title-line {\n    margin-top: -2px;\n    font-size: 36px;\n    color: rgb(204, 204, 204);\n  }\n  .goods-desc {\n    flex-direction: row;\n    justify-content: center;\n    align-items: center;\n    margin-top: 10px;\n  }\n  .goods-desc-text {\n    font-size: 24px;\n    text-align: center;\n    color: rgb(153, 153, 153);\n  }\n  .goods-scroller {\n    flex-direction: row;\n    padding-left: 10px;\n    padding-right: 10px;\n    margin-top: 30px;\n    width: 750px;\n    height: 402px;\n  }\n  .goods-container {\n    margin-left: 5px;\n    margin-right: 5px;\n    width: 210px;\n  }\n  .goods-img {\n    width: 210px;\n    height: 210px;\n  }\n  .goods-name {\n    flex-direction: row;\n    align-items: left;\n    justify-content: center;\n    margin-top: 15px;\n    padding-left: 10px;\n    padding-right: 10px;\n  }\n  .goods-name-text {\n    lines: 1;\n    font-size: 24px;\n    width: 190px;\n    color: rgb(77, 77, 77);\n    text-overflow: ellipsis;\n  }\n  .goods-price {\n    flex-direction: row;\n    align-items: flex-end;\n    justify-content: center;\n    margin-top: 18px;\n    height: 28px;\n  }\n  .goods-price-unit {\n    font-size: 20px;\n    color: rgb(249, 52, 80);\n  }\n  .goods-price-text {\n    font-size: 28px;\n    color: rgb(249, 52, 80);\n  }\n  .goods-comm {\n    flex-direction: row;\n    align-items: flex-end;\n    justify-content: center;\n    margin-top: 8px;\n    height: 44px;\n  }\n  .goods-comm-text {\n    padding-top: 4px;\n    padding-bottom: 4px;\n    font-size: 24px;\n    text-align: center;\n    color: rgb(255, 127, 147);\n    border-top-left-radius: 20px;\n    border-top-right-radius: 20px;\n    border-bottom-left-radius: 20px;\n    border-bottom-right-radius: 20px;\n    border-width: 2px;\n    border-style: solid;\n    border-color: rgb(255, 127, 147);\n    padding-right: 16px;\n    padding-left: 16px;\n  }\n  .all-title-text {\n    font-size: 28px;\n    color: rgb(26, 26, 26);\n    text-align: center;\n  }\n  .all-container {\n    margin-left: 5px;\n    margin-right: 5px;\n    width: 210px;\n    height: 352px;\n    flex-direction: column;\n    justify-content: center;\n    align-items: center;\n    background-color: rgb(243, 243, 243);\n  }\n  .all-area {\n    width: 116px;\n    height: 65px;\n  }\n  .all-desc-text {\n    margin-top: 4px;\n    padding-top: 4px;\n    font-size: 24px;\n    color: rgb(179, 179, 179);\n    text-align: center;\n    border-top-width: 2px;\n    border-color: rgb(230, 230, 230);\n    border-style: solid;\n    border-left: none;\n    border-right: none;\n    border-bottom: none;\n  }\n\n</style>\n\n<script>\n\n  var stream = weex.requireModule('stream');\n  var dialog = weex.requireModule('shopModal');\n  var shopBase = weex.requireModule('shopBase');\n\n  export default {\n    data: function() {\n      return {\n        arrImg: '//cdn1.showjoy.com/images/97/9796a887ac2b491698cae440fbdb5877.png',\n        listHeight: 300,\n        isNative: true,\n        showSlider: false,\n        advImg: '//cdn1.showjoy.com/images/66/66a4c388b4684d63b03983da5cb8f180.png.200x200.png',\n        noBannerImg: '//cdn1.showjoy.com/images/6f/6f63940a2ad34905be0c228d965cef62.jpg.750x375.jpg',\n        bannerList: [\n            {\n              \"pic\": \"//cdn1.showjoy.com/images/6f/6f63940a2ad34905be0c228d965cef62.jpg.750x375.jpg\"\n            }\n        ],\n        moduleList: [\n\n        ]\n      }\n    },\n    methods: {\n      setListHeight: function() {\n        let self = this;\n        let deviceHeight = parseInt(weex.config.env.deviceHeight);\n        let rate = weex.config.env.deviceWidth / 750;\n        let deviceScale = weex.config.env.scale;\n\n       if (weex.config.env.platform.toLowerCase() === 'web') {\n          self.isNative = false;\n          self.listHeight = parseInt(weex.config.env.deviceHeight / rate) - 122;\n        } else {\n          self.listHeight = (deviceHeight - ((64 + 50) * deviceScale)) / rate;\n        }\n\n      },\n\n      getData: function() {\n        let self = this;\n        let bannerApi = '/api/shop/activityExposure/banners';\n        let moduleApi = '/api/shop/activityExposure/sku?size=6';\n        // let bannerApi = './data.json';\n        // let moduleApi = './module.json';\n\n        stream.fetch({\n          method: 'GET',\n          type: 'json',\n          url: bannerApi\n        }, function(res) {\n          if(res.ok) {\n            if(res.data.isSuccess === 1) {\n              if(res.data.data.length > 0) {\n                self.showSlider = true;\n                self.bannerList = res.data.data;\n              }\n            } else {\n              dialog.toast({ message: '小店被挤爆啦! 请稍后再试~'});\n            }\n          } else {\n            dialog.toast({ message: '小店被挤爆啦! 请稍后再试~'});\n          }\n        });\n\n        stream.fetch({\n          method: 'GET',\n          type: 'json',\n          url: moduleApi,\n          data: {\n            size: 6\n          }\n        }, function(res) {\n          shopBase.hideLoading();\n          if(res.ok) {\n            if(res.data.isSuccess === 1) {\n              self.moduleDataHandling(res.data.data);\n            } else {\n              dialog.toast({ message: '小店被挤爆啦! 请稍后再试~'});\n            }\n\n          } else {\n            dialog.toast({ message: '小店被挤爆啦! 请稍后再试~'});\n          }\n        });\n      },\n\n      moduleDataHandling: function(json) {\n        let self = this;\n        json.forEach(function(item) {\n          item.skuInfos.forEach(function(sku) {\n            sku.commission = parseFloat(sku.commission).toFixed(2);\n            sku.shopPrice = parseFloat(sku.shopPrice).toFixed(2);\n            sku.originalPrice = parseFloat(sku.originalPrice).toFixed(2);\n          });\n        });\n\n        self.moduleList = json;\n      },\n\n      init: function() {\n        let self = this;\n        self.showSlider = true;\n\n        self.setListHeight();\n        self.getData();\n      },\n      //商品详情\n      goodsDetailAction: function(skuId, shopId) {\n        shopBase.userTrack({key:'home_hot_detail', params:{}});\n        let url = '/shop/sku/' + skuId + '.html?shopId=' + shopId;\n        shopBase.openUrl(url);\n      },\n    },\n    created() {\n      let self = this;\n      shopBase.setTitle('活动首页');\n      shopBase.showLoading('加载中');\n      self.init();\n    }\n  }\n</script>\n"],"sourceRoot":"webpack://"}]);

